*, *::before, *::after {
    box-sizing: border-box;
}

body {
    margin: 0;
    background: linear-gradient(to right, #000000, #f2e7aa);
}

.calculator-grid {
    display: grid;
    justify-content: center;
    margin-top: 2rem;
    grid-template-columns: repeat(4, 6rem);
    grid-template-rows: minmax(7rem, auto) repeat(5, 6rem);
}

.calculator-grid > button {
    cursor: pointer;
    border-radius: 40px;
    font-size: 2rem;
    border: 1px solid rgba(227, 225, 225, 0.1);
    outline: none;
    background-color: rgba(227, 225, 225, .75);
}

.calculator-grid > button:hover,
.calculator-grid > button:focus {
    background-color: rgba(227, 225, 225, .9);

}


.span-two {
    grid-column: span 2;
}

.output {
    grid-column: 1 / -1;
    background-color: rgba(0, 0, 0, .75);
    border-radius: 40px;

    display: flex;
    flex-direction: column;
    align-items: flex-end;
    justify-content: space-around;
    padding: .75rem;
    word-wrap: break-word;
    word-break: break-all;

}

.output .previous-operand {
    color: #948d8d;
    font-size: 1.5rem;
}

.output .current-operand {
    color: #fff;
    font-size: 2.5rem;
}

